
body{
    font-family: sans-serif;
}

form{
    margin: 20px;
}

label{
    display: block;
}

input{
    margin-left: 20px;
    margin-top: 20px;
}

input[type="checkbox"]{
    margin-left: 0;
    margin-right: 10px;
}

button{
    display: block;
    max-width: 200px;
    padding: 10px 20px;
    margin-top: 20px;
}

span{
    color: tomato;
    font-size: 12px;
    margin-left: 10px;
    font-family: 'tahoma';
    letter-spacing: 1px;
}
